import { BairroDTO } from './../models/domain/bairro.dto';
import { MunicipioDTO } from './../models/domain/municipio.dto';
import { EstadoDTO } from './../models/domain/estado.dto';
import { API_CONFIG } from './../config/core/api.config';
import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { Observable } from "rxjs/Rx";

@Injectable()
export class LogradouroService {

    url = `${API_CONFIG.baseUrl}/logradouro` 

    constructor(public http: HttpClient) {
    }

    consultarEstados() : Observable<EstadoDTO[]>  {
        console.log(this.url)
        return this.http.get<EstadoDTO[]>(`${this.url}/estados`);
    }

    consultarMunicipioPorEstado(estado: string) : Observable<MunicipioDTO[]>  {
        return this.http.get<MunicipioDTO[]>(`${this.url}/municipio-por-estado/${estado}`);
    }
    
    consultarBairroPorMunicipio(idMunicipio: number) : Observable<BairroDTO[]>  {
        return this.http.get<BairroDTO[]>(`${this.url}/bairro-por-municipio/${idMunicipio}`);
    }
}



