import { ComboDTO } from './../../../../../models/domain/combo.dto';
import { TrataErrorService } from './../../../../../services/trata-error.service';
import { LogradouroService } from './../../../../../services/logradouro.service';
import { BairroDTO } from './../../../../../models/domain/bairro.dto';
import { MunicipioDTO } from './../../../../../models/domain/municipio.dto';
import { EstadoDTO } from './../../../../../models/domain/estado.dto';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Component, Output, ViewChild, OnInit, EventEmitter, Input } from '@angular/core';

import { AbstractPopupComponent } from 'config/core/abstract-popup.component';
import { FormaPagamentoService } from './../../formapagamento/forma-pagamento.service';

import { TaxaEntregaService } from './../taxa-entrega.service';
import { TaxaEntregaDTO } from './../../../../../models/domain/taxa-entrega.dto';

@Component({
  selector: 'taxa-entrega-popup-modal',
  templateUrl: './taxa-entrega-popup-modal.component.html',
  styleUrls: ['./taxa-entrega-popup-modal.component.scss']

})
export class TaxaEntregaPopupModalComponent extends AbstractPopupComponent implements OnInit {


  item: TaxaEntregaDTO;
  ufs: EstadoDTO[];
  municipios: MunicipioDTO[]
  bairros: ComboDTO[];

  constructor(
    public taxaService: TaxaEntregaService,
    public logra: LogradouroService,
    public fb: FormBuilder) {
    super('taxaEntregaPopup');
  }

  ngOnInit() {
    this.criarForm();
    this.ufs = [];
    this.municipios = [];
    this.bairros = [];
    this.logra.consultarEstados().subscribe(res => this.ufs = res, error => TrataErrorService.tratarError(error));

  }

  show() {
    this.criarForm();
    this.showPopup("Cadastra taxa de entrega");
  }

  showAlteracao(item: TaxaEntregaDTO) {
    this.alterarForm(item)
    this.showPopup("Alterar taxa de entrega");

  }

  btnConfirmar(item: TaxaEntregaDTO) {
    let bairros = [] = [];
    for (let b of item.bairros) {
      let i: BairroDTO = { idBairro: b['id'], nmBairro: b['text'] };
      bairros.push(i);
    }
    item.bairros = bairros;
    this.acao(item, this.taxaService);
  }


  criarForm() {
    this.formulario = this.fb.group({
      id: [''],
      nmTaxaEntrega: ['', Validators.required],
      vlTaxaEntrega: ['', Validators.required],
      sgUf: ['', Validators.required],
      idMunicipio: ['', Validators.required],
      bairros: [''],
      emAlteracao: [false]
    })
  }


  alterarForm(itemSelecionado: TaxaEntregaDTO) {
    
    this.taxaService.findById(itemSelecionado.id).subscribe((item: TaxaEntregaDTO) => {
      this.formulario.patchValue({
        id: item.id,
        nmTaxaEntrega: item.nmTaxaEntrega,
        vlTaxaEntrega: item.vlTaxaEntrega,
        sgUf: item.sgUf,
        idMunicipio: item.idMunicipio,
        bairros: this.preencharCombo(item.bairros, 'idBairro', 'nmBairro'),
        emAlteracao: true
      })
      this.carrregarMunicipio(item.sgUf);
      this.carregarBairro(item.idMunicipio);

    }, error => TrataErrorService.tratarError(error)
    )
  }


  carrregarMunicipio(estado: string) {
    this.municipios = [];
    this.logra.consultarMunicipioPorEstado(estado).subscribe(res => this.municipios = res, error => TrataErrorService.tratarError(error));
  }

  carregarBairro(idMuncipio: number) {
    this.logra.consultarBairroPorMunicipio(idMuncipio).subscribe(res => this.bairros = this.preencharCombo(res, 'idBairro', 'nmBairro'), error => TrataErrorService.tratarError(error));
  }

  public refreshValue(value: any): void {
    this.formulario.patchValue({
      bairros: value
    })
  }
}



